export const msgcmd = {

    refreshAssets: 101,
    saveDataBefore: 102,
    saveDataAfter: 103,
    mapEnter: 104,
    mapLeave: 105,

    cmd2KeyObj: {},

    init: () => {
        for (let key in msgcmd) {
            let ac = msgcmd[key];
            if (typeof (ac) != 'number') {
                continue;
            }
            msgcmd.cmd2KeyObj[ac] = key;
        }
        console.info(msgcmd.cmd2KeyObj);
    }
};
